import { useState } from "react";
import style from "./Academics.module.css";
import { useTranslation } from "react-i18next";
import BorderBottom from "../../../../../StaffTab/components/DividerTab/MainContent/StaffAProfile/BorderBottom";
import SubjectDailyUpdate from "./SubjectDailyUpdate";
import AddAcademicRecord from "./AddAdcemicRecord/AddAcademicRecord";
import AcademicEditTime from "./AcademicDetail/AcademicEditTime";
import { useParams, Link, useLocation, useNavigate } from "react-router-dom";
import CreateButton from "../../../../../optimized/Ui/Button/CreateButton";
import { useSubjectTeachingPlanReportExport } from "../../../../../optimized/Hooks/ApiHooks/SubjectManager/subject-manager-api";
import {
  daybookBaseUrl,
  pdfCdnUrl,
} from "../../../../../optimized/Services/UrlConfig/BaseUrl";
import QvipleLoader from "../../../../../optimized/Loader/QvipleLoader";
import CustomGeneratedPdf from "../../../../../optimized/Ui/GeneratedPdf/CustomGeneratedPdf";
const Academics = ({ sid, carryParentState, isDepartment }) => {
  const { t } = useTranslation();
  const params = useParams();
  const getQuery = useLocation();
  const navigate = useNavigate();
  const [addRecord, setAddRecord] = useState(false);
  const [editCount, setEditCount] = useState(false);
  const [reftechStatus, setReftechStatus] = useState(false);
  const [disabled, setDisabled] = useState(false);
  const [openPdf, setOpenPdf] = useState("");

  const [subjectTeachingPlanReportExport] =
    useSubjectTeachingPlanReportExport();

  const onRecordToggle = () => {
    setAddRecord((pre) => !pre);
  };
  const onEditToggle = () => {
    setEditCount((pre) => !pre);
  };

  const onRefetch = () => {
    setReftechStatus(true);
  };

  const onNavigate = () => {
    let url = `/q/${params.username}/member/subject/${params.sname}?a=co-setting`;
    navigate(url, {
      state: getQuery.state,
    });
  };
  const onReportExport = () => {
    if (sid) {
      setDisabled((pre) => !pre);
      subjectTeachingPlanReportExport({
        sid: sid,
        is_pdf: "YES",
      })
        .then((res) => {
          if (res?.data?.excel_key) {
            setTimeout(() => {
              setDisabled((pre) => !pre);
              // if (range.is_pdf) {
              setOpenPdf(res?.data?.excel_key);
              // } else {
              //   const newWindow = window.open(
              //     `${pdfCdnUrl}/${res?.data?.excel_key}`,
              //     "_blank",
              //     "noopener,noreferrer"
              //   );
              //   if (newWindow) newWindow.opener = null;
              //   onClose();
              // }
            }, 2500);
          } else {
            setDisabled((pre) => !pre);
          }
        })
        .catch(() => {});
    }
  };
  const onClosePdf = () => {
    setOpenPdf("");
  };
  return (
    <>
      <div className={style.subject_academic_container}>
        <div className={style.subject_academic_title}>
          <h6>{t("academics_teachings")}</h6>
          <div className={style.subject_academic_link}>
            {isDepartment === "CLASS_TEACHER" ? (
              <Link
                to={`/q/${params.username}/member/class/${params.cname}?a=subject-academic-detail`}
                state={{
                  ...carryParentState,
                  subjectId: sid,
                  isView: "DEPARTMENT_HEAD",
                }}
              >
                <h6
                  className={style.add_record_lecture}
                  style={{
                    margin: "0",
                  }}
                >
                  {t("view_academic_detail")}
                </h6>
              </Link>
            ) : isDepartment === "DEPARTMENT_HEAD" ? (
              <Link
                to={`/q/${params.username}/member/department/${params.deptname}?a=subject-academic-detail`}
                state={{
                  ...carryParentState,
                  subjectId: sid,
                  isView: "DEPARTMENT_HEAD",
                }}
              >
                <h6
                  className={style.add_record_lecture}
                  style={{
                    margin: "0",
                  }}
                >
                  {t("view_academic_detail")}
                </h6>
              </Link>
            ) : (
              <>
                <Link
                  to={`/q/${params.username}/member/subject/${params.sname}?a=subject-academic`}
                  state={{
                    ...carryParentState,
                    subjectId: sid,
                    isView: isDepartment
                      ? "DEPARTMENT_HEAD"
                      : "SUBJECT_TEACHER",
                    isSubType: "SUBJECT_TEACHER_INSTITUTE",
                  }}
                >
                  <h6
                    className={style.add_record_lecture}
                    style={{
                      margin: "0",
                    }}
                  >
                    {t("view_academic_detail")}
                  </h6>
                </Link>
              </>
            )}
          </div>
        </div>
        <BorderBottom
          customStyle={{
            width: "100%",
            marginTop: "0.7rem",
            marginBottom: "0.7rem",
          }}
        />
        <div className={style.record_lecture}>
          <CreateButton
            label="export_report"
            customStyle={{
              margin: "0",
              padding: "0.3rem 0.5rem",
            }}
            onAction={onReportExport}
          />
          {isDepartment ? null : (
            <>
              <CreateButton
                label="create_co"
                customStyle={{
                  margin: "0",
                  padding: "0.3rem 0.5rem",
                }}
                onAction={onNavigate}
              />
              <CreateButton
                label="record_lecture_teachings"
                customStyle={{
                  margin: "0",
                  padding: "0.3rem 0.5rem",
                }}
                onAction={onRecordToggle}
              />
            </>
          )}
        </div>
        {addRecord && (
          <AddAcademicRecord
            onClose={onRecordToggle}
            sid={sid}
            onRefetch={onRefetch}
          />
        )}
        {editCount && <AcademicEditTime onClose={onEditToggle} sid={sid} />}
        <SubjectDailyUpdate
          sid={sid}
          reftechStatus={reftechStatus}
          setReftechStatus={setReftechStatus}
          isDepartment={isDepartment}
        />
      </div>
      {disabled && <QvipleLoader />}
      {openPdf ? (
        <CustomGeneratedPdf
          subUrl={daybookBaseUrl}
          documentUrl={openPdf}
          fileName={openPdf}
          onClose={onClosePdf}
        />
      ) : null}
    </>
  );
};

export default Academics;
